/* NAVITEM */

.nav-item {
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.2rem 1.2rem;
    font-weight: 300;
    font-size: 0.95rem;
    line-height: 1;
}

.nav-link {
    text-decoration: none;
    color: var(--white);
    cursor: pointer;
    display: flex;
    align-items: center;
}


.nav-item-text {
    margin-right: 0.4rem;
}


.active {
    color: var(--main-blue);
}